

































import {Vue, Component} from 'vue-property-decorator';
import gsap from 'gsap';
import Textmd from './Text.md';
@Component({
  components: {
    Textmd,
  },
})
export default class Texts extends Vue {
  private animation: any = { xturbulence: 0.01 , yturbulence:  2, opacity: 0, b1ur: 20} ;
  private lyrics: string[] = [' - ', '', '', '', '', '', '', ' ', '', '', '', '', '', ' ', '', '', '', '', ' ', '  ', ' ', '', ' ', '', '', '', '', '', '', '', '', '', ' ', '', '', '  ', '', '', '', ' ', ' ', '', '', ' ', '', '', '', '', '', '', '', '', ' ', '', ' '];
  private count: number = 0;
  private currentString: string = '';
  private _interval: any;
  private isBlur: boolean = false;
  private mounted() {
    // gsap.fromTo('p',{opacity: 0}, {opacity: 1,duration: 1, ease: 'none'})
    // gsap.to(this.frequency,{xturbulence: 0.002,yturbulence: 0,duration: 1, ease: 'none'})
    // const interval = setInterval( ()=>{ this.update() } , 4000)
    // this._blur()
  }
  private reset() {
    this.currentString = '';
    this.animation = { xturbulence: 0.01 , yturbulence:  2, opacity: 0, b1ur: 20};
    clearInterval(this._interval);
    this.count = 0;
    this._interval = setInterval( () => { this.update(); } , 4000);
  }
  private update(): void {
    if (this.count >= this.lyrics.length - 1) { return; }
    this.currentString = this.lyrics[this.count];
    this.count++;
  }
  private _guitar(): void {
    this.isBlur = false;
    this.reset();
    gsap.to(this.animation, {xturbulence: 0, yturbulence: 0, opacity: 1, b1ur: 0, duration: 2, ease: 'expo', yoyo: true, repeat: -1});
  }
  private _blur(): void {
    this.isBlur = true;
    this.reset();
    gsap.to(this.animation, {xturbulence: 0, yturbulence: 0, opacity: 1, b1ur: 0, duration: 2, ease: 'slow', yoyo: true, repeat: -1});
  }
}
